import axios from 'axios';
import VueRouter from '../router'
import Vue from 'vue'
import {getToken} from "@/utils/auth";

const config = {
    //baseURL: 'http://10.17.76.200:3000', // api的base_url
    //baseURL: 'http://159.75.0.42:80',
    timeout: 5*1000,
    headers: {
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
        // 'X-Requested-With': 'XMLHttpRequest',
        // 'X-account-token': myToken
    },
}
const https = axios.create(config);


// 添加请求拦截器
https.interceptors.request.use(function (config) {
    //console.log(getToken())
    if (getToken()) {
        //console.log('设置请求头');
        config.headers['Authorization'] = 'Bearer ' + getToken(); // 让每个请求携带自定义token 请根据实际情况自行修改
        //config.headers['sessionid'] = localStorage.getItem('session_id'); // 让每个请求携带自定义token 请根据实际情况自行修改
    }

    return config;
}, function (error) {
    // 对请求错误做些什么

    return Promise.reject(error);
});

// 添加响应拦截器
https.interceptors.response.use(function (response) {
    return response;
}, function (error) {
    // 对响应错误做点什么o
    console.log(error.response,'看一下返回来的状态码')
    switch (error.response.status) {
        case 401 :{
            Vue.prototype.$notify.error({
                title: '错误',
                message: '您的身份已过期，请重新登录！',
                duration: 2000,
                onClose: () => {
                    VueRouter.push('login').then(res=> {
                        console.log(res)
                    });
                }
            });
            break;
        }
        case 500: {
            Vue.prototype.$message({
                type: 'warning',
                message: error.response.statusText + ',请稍后再试！'
            });
            //VueRouter.push('/404').then()
        }
    }
    return Promise.reject(error);
});



export default https
