const TokenKey = 'Admin-Token';
const UserInfoKey = 'User-Info';
const AfterLoadingRouters = 'After-Loading-Routers';

//存储token
export function getToken() {
    return JSON.parse(localStorage.getItem(TokenKey))
}

export function setToken(token) {
    return localStorage.setItem(TokenKey, JSON.stringify(token));
}

export function removeToken() {
    return localStorage.removeItem(TokenKey);
}

//存储用户信息
export function getUserInfo() {
    return JSON.parse(localStorage.getItem(UserInfoKey))
}

export function setUserInfo(info) {
    return localStorage.setItem(UserInfoKey, JSON.stringify(info));
}

export function removeUserInfo() {
    return localStorage.removeItem(UserInfoKey);
}

//存储获取的路由表
export function getAfterLoadingRouters() {
    return JSON.parse(localStorage.getItem(AfterLoadingRouters))
}

export function setAfterLoadingRouters(info) {
    return localStorage.setItem(AfterLoadingRouters, JSON.stringify(info));
}

export function removeAfterLoadingRouters() {
    return localStorage.removeItem(AfterLoadingRouters);
}